<template>
  <div class="about">
    <van-search
      v-model.trim="searchText"
      @keyup.enter="getSearch()"
      placeholder="请输入要搜索的内容"
    />
    <template v-if="!searchText">
      <div>
        <h1>热门搜索</h1>
        <ul class="hots" v-if="keywords">
          <li
            class="result"
            v-for="(item, index) in keywords"
            :key="index"
            @click="searchTo(item)"
          >
            {{ item.kw }}
          </li>
        </ul>
        <h1>搜索历史</h1>
      </div>
    </template>

    <template v-if="serachResult && searchText">
      <div>
        <ul>
          <CardItem
            :item="item"
            v-for="(item, index) in serachResult"
            :key="index"
          ></CardItem>
        </ul>
      </div>
    </template>
  </div>
</template>

<script>
import CardItem from "@/components/CardItem.vue";
export default {
  components: {
    CardItem
  },
  data() {
    return {
      searchText: null,
      keywords: null,
      serachResult: null
    };
  },
  methods: {
    getHotSearch() {
      this.axios
        .get("https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/search")
        .then(response => {
          // window.console.log("hot",response);
          this.keywords = response.data.data.recommend_keywords;
        });
    },
    searchTo(item) {
      this.$router.push({
        path: "/searchResult",
        query: { obj: item }
      });
    },
    getSearch() {
      this.axios
        .get(
          "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/search?kw=" +
            this.searchText
        )
        .then(response => {
          this.serachResult = response.data.data.result.list;
        });
    }
  },
  created() {
    this.getHotSearch();
    // this.getSearch();
  },
  watch: {
    searchText(newValue) {
      if (newValue) {
        this.getSearch();
      }
    }
  }
};
</script>

<style lang="less" scoped>
.about {
  h1 {
    font-size: 14px;
    padding: 10px 15px;
  }
  ul.hots {
    margin-top: 10px;
    margin-left: 15px;
    .result {
      display: inline-block;
      padding: 5px 10px;
      border: 1px solid #ccc;
      margin-right: 10px;
      margin-bottom: 10px;
      text-align: center;
      border-radius: 5px;
      background: #222;
      color: #fff;
      font-size: 12px;
    }
  }
}
</style>
