<template v-if="results">
  <div class="result">
    <h2 class="btn">
      <van-icon name="arrow-left" @click="$router.back()" />
    </h2>
    <ul>
      <CardItem
        :item="item"
        v-for="(item, index) in results"
        :key="index"
      ></CardItem>
    </ul>
  </div>
</template>

<script>
import CardItem from "@/components/CardItem.vue";
export default {
  components: {
    CardItem
  },
  data() {
    return {
      results: null
    };
  },
  methods: {
    getResult(kw) {
      this.axios
        .get(
          "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/search?kw=" +
            kw
        )
        .then(response => {
          window.console.log("re", response);
          this.results = response.data.data.result.list;
        });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.getResult(to.query.obj.kw);
    });
  }
};
</script>

