<template>
  <div class="relateplay">
    <h2 class="btn">
      <van-icon name="arrow-left" @click="$router.back()" />
    </h2>
    <ul>
      <template v-if="cateId">
        <CardItem
          :item="item"
          v-for="(item, index) in cateId"
          :key="index"
        ></CardItem>
      </template>
       <template v-else>
        <CardItem
          :item="item"
          v-for="(item, index) in relateLists"
          :key="index"
        ></CardItem>
      </template>
    </ul>
  </div>
</template>

<script>
import CardItem from "@/components/CardItem.vue";
export default {
  components: {
    CardItem
  },
  data() {
    return {
      relateLists: null,
      cateId:null
    };
  },
  methods: {
    getRelatePlay(id) {
      // var url;
      // if (parseInt(this.$route.query.obj).toString() != "NaN") {
      //   url =
      //     "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/post/getPostInCate?p=1&size=100&cateid=" +
      //     this.$route.query.obj.cateid;
      // } else {
      //   url =
      //     "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/post/getPostByTab&p=1&size=10&tab=" +
      //     this.$route.query.obj.tab;
      // }

      this.axios
        .get(
          "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/post/getPostByTab&p=1&size=10&tab=" +
            // this.$route.query.obj.tab
            id
        )
        .then(response => {
          // window.console.log(response);
          this.relateLists = response.data.data;
        });
    },
    getCateId(id) {
      this.axios
        .get(
          "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/post/getPostInCate?p=1&size=100&cateid=" +
            id
            // this.$route.query.obj.cateid
        )
        .then(response => {
          window.console.log("id",response);
          this.cateId = response.data.data;
        });
    }
  },
  created() {
    this.getRelatePlay();
    this. getCateId();
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.getRelatePlay(to.query.obj.tab);
      vm.getCateId(to.query.obj.cateid);
    });
  }
};
</script>

<style></style>
