<template>
  <div class="play" v-if="playLists">
    <h2 class="btn">
      <van-icon name="arrow-left" @click="$router.back()" />
    </h2>
    <!-- <img :src="playLists.image" alt="" /> -->
    <div class="vedio" v-if="playLists.content_video[0].progressive[0]">
      <video
        :src="playLists.content_video[0].progressive[0].https_url"
        controls
        autoplay
      ></video>
      <!-- {{playLists.content_video[0].progressive[0].https_url}} -->
    </div>
    <!-- 视频简介 -->
    <div class="content">
      <h3 class="top-title">{{ playLists.title }}</h3>
      <div class="type">{{ playLists.cate[0] }}</div>
      <div class="cantainer">{{ playLists.intro }}</div>
    </div>

    <!-- 相关视频 -->
    <div
      class="relate"
      v-for="(item, index) in playLists.relate_video"
      :key="index"
    >
      <p class="names">{{ item.name }}</p>
      <ul class="lates">
        <Relate
          :item="item"
          v-for="(item, index) in item.list"
          :key="index"
          @xxx="relateList = $event"
        >
        </Relate>
      </ul>
    </div>
  </div>
</template>

<script>
import Relate from "@/components/Relate.vue";
export default {
  components: {
    Relate
  },
  data() {
    return {
      playLists: null,
      relateList: null
    };
  },
  // computed: {
  //   currentPlay() {
  //     return this.$store.state.currentPlay;
  //   }
  // },
  methods: {
    getPlay(id) {
      // window.console.log(this.$route);
      this.axios
        .get(
          "http://api.kele8.cn/agent/https://app.vmovier.com/apiv3/post/view?postid=" +
            id
        )
        .then(response => {
          window.console.log(response);
          this.playLists = response.data.data;
        });
    },
    routerToRelate(item) {
      this.$router.push({ path: "/relatePlay", query: { id: item.postid } });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.getPlay(to.query.obj.postid);
    });
  },

  watch: {
    relateList() {
      this.getPlay(this.relateList);
    }
  }
};
</script>

<style lang="less">
.play {
  position: relative;
  h2.btn {
    position: absolute;
    z-index: 5;
    top: 5px;
    left: 10px;
    font-size: 18px;
    font-weight: bold;
    line-height: 2em;
    color: #fff;
  }
  .vedio {
    video {
      width: 100%;
    }
  }
  .content {
    margin-top: 20px;
    border-bottom: 1px solid #ccc;
    .top-title {
      font-size: 16px;
      line-height: 2em;
      text-align: center;
      font-weight: bold;
    }
    .type {
      font-size: 12px;
      text-align: center;
      line-height: 1.5em;
    }
    .cantainer {
      font-size: 14px;
      line-height: 2em;
      padding: 20px 15px;
    }
  }
  .relate {
    padding: 10px 15px;
    .names {
      font-size: 14px;
      font-weight: bold;
      padding: 10px 0;
    }
    .lates {
      width: 100%;
      display: -webkit-box;
      overflow: scroll;
      li.lists {
        position: relative;
        margin-right: 10px;
        width: 50%;
        img {
          width: 100%;
        }
        p {
          position: absolute;
          left: 5px;
          bottom: 10px;
          width: 100%;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          font-size: 12px;
          color: #fff;
        }
      }
    }
  }
}
</style>
